import { Component, OnInit, HostListener, ElementRef } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router'
import { CloudReportService } from '../report.service';
import { Location } from '@angular/common';

@Component({
  selector: 'app-cloudreportcheckdetailcomponent',
  template: '',
  styleUrls: ['component.scss']
})
export class CloudReportCheckCategoryComponent implements OnInit {

  regions: string[];
  checkCategories: object[];
  selectedRegion: string;
  globalService: boolean;
  regionSelectValidate: boolean = false;
  service: string

  constructor(private route: ActivatedRoute,
    private cloudReportService: CloudReportService,
    private elementRef: ElementRef,
    private router: Router
  ) { }

  ngOnInit() {
    this.service = this.route.snapshot.paramMap.get('service');
    const regionsHaveData = this.cloudReportService.getRegionsHaveData('aws.' + this.service);
    // console.log(regionsHaveData);
    if (regionsHaveData.length === 1) {
      this.selectedRegion = regionsHaveData[0];
      this.regionSelectValidate = true;
      this.regions = regionsHaveData;
      // this.cloudReportService.manageRegion(undefined, 'aws.' + this.service);
    }
    else {
      this.regions = regionsHaveData;
      this.selectedRegion = this.cloudReportService.manageRegion(undefined, 'aws.' + this.service);
    }
    this.checkCategories = this.cloudReportService.getCheckCategoryData('aws.' + this.service, this.selectedRegion);
  }

  onRegionChange(region) {
    this.cloudReportService.manageRegion(region);
    this.ngOnInit();
  }

  goToCheckDetail(checkCategory) {
    this.router.navigate(['/cloudReport/checkDetail',
      {
        checkCategory: checkCategory['checkCategoryName'],
        region: this.selectedRegion,
        service: this.service
      }
    ]);
  }

}